import React, { Component } from 'react';
import Project from "../project/project";

import project_data from "../../data/project_data.json"
import Skills from "../skills/skills"
import "./projects.css"

class Projects extends Component {
    state = { }
    render() { 
        // console.log(project_data);

        return ( 
            <div className = "projects">
            <div className = "projects-overview-title">Projects Overview</div>
            <div className = "projects-overview"><Skills></Skills></div>
                {project_data.map(p => 
                    <Project key = {p.title} title = {p.title} 
                             description = {p.description} 
                             info = {p.info} 
                             members = {p.members} 
                             repoLink = {p.repoLink}
                             projectPage = {p.projectPage}
                             moreInfo = {p.moreInfo}
                             languages = {p.languages}>
                    </Project>
                )}
            </div>
        );
    }
}
 
export default Projects;