import React, { Component } from 'react';
import "./verticalNonLinearStepper.css"

import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import StepButton from '@material-ui/core/StepButton';
import Typography from '@material-ui/core/Typography';
import { createMuiTheme } from '@material-ui/core/styles';

import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';

const theme = createMuiTheme({
    palette: {
        primary: {main: "#ff652f"},
    },
    typography: { useNextVariants: true },
});

class VerticalNonLinearStepper extends Component {
    state = { 
        activeStep: 0
    }

    render() {
        const steps = this.getSteps();
        const { activeStep } = this.state;
    
        return (
            <div className = "stepper">
                <MuiThemeProvider theme={theme}>
                <Stepper nonLinear activeStep = {activeStep} 
                         orientation = "vertical" 
                         className ="stepper__vertical">
                    {steps.map((label, index) => {
                        return (
                            <Step key = {label}>
                                <StepButton onClick = {this.handleStep(index)} className = "stepper__button">
                                    <StepLabel><span className = "stepper__step-label-primary">{label.primary}</span></StepLabel>
                                    <StepLabel><span className = "stepper__step-label-secondary">{label.secondary}</span></StepLabel>
                                </StepButton>
                                <StepContent>
                                    <Typography className = "stepper__step-content-subheading">{this.getStepContent(index).subheading}</Typography>
                                    <Typography className = "stepper__step-content-body">{this.getStepContent(index).body}</Typography>
                                </StepContent>
                            </Step>
                        )
                    })}
                    </Stepper>
                    </MuiThemeProvider>
            </div>
        );
    }

    getSteps() {
        return [
            {primary: "Software Developer Research Assistant",
             secondary: "Fall 2017 - Fall 2018"},
            {primary: "Research Intern",
             secondary: "Summer 2017"}]
    }

    getStepContent(step) {
        switch (step) {
            case 0:
                return {
                    subheading: "University of Illinois at Chicago | Chicago, IL",
                    body: <React.Fragment>
                        <br/>
                        Collaborated on a multi-university project (Project SENSEI) under Professor Dan Sandin, to produce a 360 3D camera with minimal stitching artifacts. 
                        <br/><br/>
                        Used Unity/Blender/Unreal Engine to create multiple virtual camera designs, which were used to capture the scenes with animations to test panoramic stitching algorithms and test the most optimal camera rig design. 
                        <br/><br/>
                        Created depth files (ply format) to check the correctness and accuracy of the camera’s assumed depth. 
                        <br/><br/>
                        Created C#, C++, and python scripts for taking images / image processing and process streamlining. 
                        <br/><br/>
                        <li>SENSEI Project: <a rel="noopener noreferrer" target="_blank" href = "https://www.evl.uic.edu/entry.php?id=1191">EVL</a></li>
                        <li>SENSEI Project More Info: <a rel="noopener noreferrer" target="_blank" href = "https://www.evl.uic.edu/entry.php?id=2229">EVL</a></li>
                        <li>Full Project: <a rel="noopener noreferrer" target="_blank" href = "https://github.com/BluuArc/vast-challenge-2017">Repo</a></li>
                    </React.Fragment>
                };
            case 1:
                return {
                    subheading: "University of Illinois at Chicago | Chicago, IL",
                    body: <React.Fragment>
                        <br/>
                        Designed a web-based tool to submit as an entry to VAST Challenge 2017. The project was divided into three sub-groups and later combined as a whole.
                        <br/><br/>
                        <li>Mini Challenge 1: <a rel="noopener noreferrer" target="_blank" href = "https://github.com/dkirilov4/VAST-Challenge-MC1">Repo</a></li>
                        <li>Full Project: <a rel="noopener noreferrer" target="_blank" href = "https://github.com/BluuArc/vast-challenge-2017">Repo</a></li>
                        <li>UIC Newspaper Article: <a rel="noopener noreferrer" target="_blank" href = "https://today.uic.edu/uicevl-receives-honorable-mentions-in-vast-challenge">Article</a></li>
                        
                </React.Fragment>
                };
            default:
                return {
                    subheading: "Unknown step",
                    body: "Unknown step"
                };
        }
    }

    handleStep = step => () => {
        this.setState({activeStep: step})
    }
}
 
export default (VerticalNonLinearStepper);