import React, { Component } from 'react';
import "./info.css"
import VerticalNonLinearStepper from "../verticalNonLinearStepper/verticalNonLinearStepper"
import DataTable from '../datatable/datatable';
import Skills from '../skills/skills';

class Info extends Component {
    state = {  }
    render() { 
        return (
            <div className = "info m1">
                <div className = "info__employment">
                    <div className = "info-title"> Employment</div>
                    <VerticalNonLinearStepper/>
                </div>
                <div className = "info__education">
                    <div className = "info-title"> Education</div>
                    <div className = "education-background">
                        <div className = "education-background-primary">BS in Computer Science at University of Illinois at Chicago</div>
                        <div className = "education-background-secondary">Fall 2015 - Fall 2018</div>
                        <div className = "divider"><hr></hr></div>
                        <DataTable></DataTable>
                    </div>
                    
                </div>
                <div className = "info__skills">
                    <div className = "info-title"> Technical Skills</div>
                    <div className = "skills-container">
                        <div className = "skills-text-primary">Languages:</div>
                        <div className = "skills-text-secondary">JavaScript, HTML, CSS, R, SQL, C, C++, C#, Java, Python</div>
                        <br/>

                        <div className = "skills-text-primary">Frameworks/Libraries:</div>
                        <div className = "skills-text-secondary">React, .NET, Node, TensorFlow, D3, ThreeJS, SocketIO, Vuforia, CAVE 2</div>
                        <br/>

                        <div className = "skills-text-primary">Tools:</div>
                        <div className = "skills-text-secondary">MS Visual Studio, Eclipse, NetBeans, Unity 3D , JUnit Testing</div>

                        <br/>
                        <div className = "skills-text-primary">Projects Overview:</div>
                        <Skills></Skills>
                    </div>
                        
                </div>
            </div>
        );
    }
}
 
export default Info;