import React, { Component } from 'react';
import "./contact.css"

class Contact extends Component {
    state = {  }
    render() { 
        return (
            <div className = "contact m1">
                <div className = "contact-title"> Contact Me</div>
                <div className = "contact-options">
                    <button className = "contact-btn">
                        <a href = "mailto: dimitarikirilov@gmail.com">
                            <div className = "contact__option-email">
                                <i className="fa fa-envelope-o" aria-hidden="true"></i>
                            </div>
                        </a>
                    </button>

                    <button className = "contact-btn">
                        <a rel="noopener noreferrer" target="_blank" href = "https://www.linkedin.com/in/dimitar-kirilov/">
                            <div className = "contact__option-linkedin">
                                <i className="fa fa-linkedin-square" aria-hidden="true"></i>
                            </div>
                        </a>
                    </button>

                    <button className = "contact-btn">
                        <a rel="noopener noreferrer" target="_blank" href = "https://github.com/dkirilov4/">
                            <div className = "contact__option-github">
                            <i className="fa fa-github" aria-hidden="true"></i>
                            </div>
                        </a>
                    </button>
                </div>
            </div>
        );
    }
}
 
export default Contact;